import { Component, Inject, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MatBottomSheetRef, MAT_BOTTOM_SHEET_DATA } from '@angular/material/bottom-sheet';
import { MatSnackBar } from '@angular/material/snack-bar';
import { EnumerationModel } from '@models/enumeration.model';
import { HttpResponseModel } from '@models/http/response.model';
import { AuthenticationService } from '@services/authentication.service';
import { ConfigService } from '@services/config.service';
import { EnumerationService } from '@services/enumeration.service';

@Component({
  selector: 'app-enumeration-play',
  templateUrl: './enumeration-play.component.html',
  styleUrls: ['./enumeration-play.component.css']
})
export class EnumerationPlayComponent implements OnInit {
  sse = '';
  action = '';

  efg: FormGroup = new FormGroup({
    label: new FormControl(
      '',
      [
        Validators.required,
        Validators.pattern(/^[a-zA-Z0-9\s]{1,25}$/),
      ],
    ),
  });

  dfg: FormGroup;

  constructor(
    @Inject(MAT_BOTTOM_SHEET_DATA) public data: any,
    private $authentication: AuthenticationService,
    private $enumeration: EnumerationService,
    private $snackBar: MatSnackBar,
    private $bottomSheetRef: MatBottomSheetRef<EnumerationPlayComponent>,
    public config: ConfigService,
  ) {
    if (data.enumeration == null) {
      this.action = 'Add';

      this.dfg = new FormGroup({
      });
    } else {
      this.action = 'Update';

      this.dfg = new FormGroup({
        label: new FormControl(
          '',
          [
            Validators.required,
            Validators.pattern(data.enumeration.label),
          ],
        ),
      });

      this.efg.patchValue({
        ...this.data.enumeration,
      });
    }

    // ACL
    const role = this.$authentication.user?.role;

    if (role) {
      if (
        (this.config.role.rp == role.id) == false
      ) {
        this.efg.disable();
        this.dfg.disable();
      }
    }
  }

  ngOnInit(): void {
  }

  play(event: any): void {
    event.preventDefault();

    if (this.efg.valid) {
      this.sse = '';
      this.efg.disable();
      switch (this.action) {
        case 'Add':
          this.$enumeration.addEnumeration(this.efg.value).subscribe(
            () => {
              this.efg.enable();
              this.$snackBar.open(`Enumeration added`, '', {
                duration: 3000,
              });
              this.$bottomSheetRef.dismiss();
            },
            (error: HttpResponseModel) => {
              this.efg.enable();
              this.sse = error.message;
            }
          );
          break;
        case 'Update':
          this.$enumeration.updateEnumeration(this.data.enumeration.id, this.efg.value).subscribe(
            () => {
              this.efg.enable();
              this.$snackBar.open(`Enumeration updated`, '', {
                duration: 3000,
              });
            },
            (error: HttpResponseModel) => {
              this.efg.enable();
              this.sse = error.message;
            }
          );
          break;
      }
    }
  }

  delete(event: any): void {
    event.preventDefault();

    if (this.dfg.valid) {
      this.sse = '';
      this.dfg.disable();
      this.$enumeration.deleteEnumeration(this.data.enumeration.id).subscribe(
        () => {
          this.dfg.enable();
          this.$snackBar.open(`Enumeration and associated information deleted`, '', {
            duration: 3000,
          });
          this.$bottomSheetRef.dismiss();
        },
        (error: HttpResponseModel) => {
          this.dfg.enable();
          this.sse = error.message;
        }
      );
    }
  }

  dismiss(event: MouseEvent): void {
    this.$bottomSheetRef.dismiss();
    event.preventDefault();
  }
}
