import { AfterContentInit, Component, Inject, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MatBottomSheetRef, MAT_BOTTOM_SHEET_DATA } from '@angular/material/bottom-sheet';
import { MatSnackBar } from '@angular/material/snack-bar';
import { FeedbackModel } from '@models/feedback.model';
import { EnumerationModel } from '@models/enumeration.model';
import { HttpResponseModel } from '@models/http/response.model';
import { AuthenticationService } from '@services/authentication.service';
import { ConfigService } from '@services/config.service';
import { FeedbackService } from '@services/feedback.service';
import { EnumerationService } from '@services/enumeration.service';

@Component({
  selector: 'app-feedback-play',
  templateUrl: './feedback-play.component.html',
  styleUrls: ['./feedback-play.component.css']
})
export class FeedbackPlayComponent implements OnInit, AfterContentInit {
  sse = '';
  action = '';

  enumerations: EnumerationModel[] = [];

  ffg: FormGroup = new FormGroup({
    question: new FormControl(
      '',
      [
        Validators.required,
        Validators.pattern(/^.{1,125}$/),
      ],
    ),
    enumeration_ids: new FormControl(
      [],
      [
      ],
    ),
  });

  dfg: FormGroup;

  constructor(
    @Inject(MAT_BOTTOM_SHEET_DATA) public data: any,
    private $authentication: AuthenticationService,
    private $feedback: FeedbackService,
    private $enumeration: EnumerationService,
    private $snackBar: MatSnackBar,
    private $bottomSheetRef: MatBottomSheetRef<FeedbackPlayComponent>,
    public config: ConfigService,
  ) {
    if (data.feedback == null) {
      this.action = 'Add';

      this.dfg = new FormGroup({
      });
    } else {
      this.action = 'Update';

      this.dfg = new FormGroup({
        question: new FormControl(
          '',
          [
            Validators.required,
            Validators.pattern(data.feedback.question),
          ],
        ),
      });

      this.ffg.patchValue({
        ...this.data.feedback,
      });
    }

    // ACL
    const role = this.$authentication.user?.role;

    if (role) {
      if (
        (this.config.role.rp == role.id) == false
      ) {
        this.ffg.disable();
        this.dfg.disable();
      }
    }
  }

  ngOnInit(): void {
  }

  ngAfterContentInit(): void {
    this.getEnumerations();
  }

  getEnumerations(): void {
    this.$enumeration
      .getEnumerations()
      .subscribe((enumerations: EnumerationModel[]) => {
        this.enumerations = enumerations;
      });
  }


  play(event: any): void {
    event.preventDefault();

    if (this.ffg.valid) {
      this.sse = '';
      this.ffg.disable();
      switch (this.action) {
        case 'Add':
          this.$feedback.addFeedback(this.ffg.value).subscribe(
            (result: HttpResponseModel) => {
              this.ffg.enable();
              this.$snackBar.open(`Feedback added`, '', {
                duration: 3000,
              });
              this.$bottomSheetRef.dismiss();
            },
            (error: HttpResponseModel) => {
              this.ffg.enable();
              this.sse = error.message;
            }
          );
          break;
        case 'Update':
          this.$feedback.updateFeedback(this.data.feedback.id, this.ffg.value).subscribe(
            (result: HttpResponseModel) => {
              this.ffg.enable();
              this.$snackBar.open(`Feedback updated`, '', {
                duration: 3000,
              });
            },
            (error: HttpResponseModel) => {
              this.ffg.enable();
              this.sse = error.message;
            }
          );
          break;
      }
    }
  }

  delete(event: any): void {
    event.preventDefault();

    if (this.dfg.valid) {
      this.sse = '';
      this.dfg.disable();
      this.$feedback.deleteFeedback(this.data.feedback.id).subscribe(
        (result: HttpResponseModel) => {
          this.dfg.enable();
          this.$snackBar.open(`Feedback and associated information deleted`, '', {
            duration: 3000,
          });
          this.$bottomSheetRef.dismiss();
        },
        (error: HttpResponseModel) => {
          this.dfg.enable();
          this.sse = error.message;
        }
      );
    }
  }

  dismiss(event: MouseEvent): void {
    this.$bottomSheetRef.dismiss();
    event.preventDefault();
  }
}
