import { Injectable } from '@angular/core';
import { HttpResponseModel } from '@models/http/response.model';
import { EnumerationModel } from '@models/enumeration.model';
import { BehaviorSubject, Observable } from 'rxjs';
import { ApiService } from '@services/api.service';

@Injectable({
  providedIn: 'root'
})
export class EnumerationService {
  $init = false;
  $dirty: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
  $enumerations: BehaviorSubject<EnumerationModel[]> = new BehaviorSubject<EnumerationModel[]>([]);

  constructor(private $api: ApiService) {
    this.$dirty.subscribe(
      (dirty: boolean) => {
        if (dirty) {
          this.$api.get('/enumeration/all').subscribe(
            (result: HttpResponseModel) => {
              this.$dirty.next(false);

              this.$enumerations.next(
                result.data.enumerations
              );
            },
            (error: HttpResponseModel) => {
            }
          );
        }
      }
    );
  }

  getEnumerations(): Observable<EnumerationModel[]> {
    if (this.$init === false || (this.$enumerations.getValue().length === 0 && this.$dirty.getValue() === false)) {
      this.$init = true;

      this.$dirty.next(true);
    }

    return this.$enumerations;
  }

  addEnumeration(enumeration: any): Observable<HttpResponseModel> {
    return this.$api
      .put('/enumeration', {
        enumeration,
      });
  }

  updateEnumeration(
    id: number,
    enumeration: any
  ): Observable<HttpResponseModel> {
    return this.$api
      .patch(`/enumeration/${id}`, {
        enumeration,
      });
  }

  deleteEnumeration(id: number): Observable<HttpResponseModel> {
    return this.$api.delete(`/enumeration/${id}`);
  }
}
