import { Injectable } from '@angular/core';
import { HttpResponseModel } from '@models/http/response.model';
import { FeedbackModel } from '@models/feedback.model';
import { BehaviorSubject, Observable } from 'rxjs';
import { ApiService } from '@services/api.service';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class FeedbackService {
  $init = false;
  $dirty: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
  $feedbacks: BehaviorSubject<FeedbackModel[]> = new BehaviorSubject<FeedbackModel[]>([]);

  constructor(private $api: ApiService) {
    this.$dirty.subscribe(
      (dirty: boolean) => {
        if (dirty) {
          this.$api.get('/feedback/all').subscribe(
            (result: HttpResponseModel) => {
              this.$dirty.next(false);

              this.$feedbacks.next(
                result.data.feedbacks
              );
            },
            (error: HttpResponseModel) => {
            }
          );
        }
      }
    );
  }

  getFeedbacks(): Observable<FeedbackModel[]> {
    if (this.$init === false || (this.$feedbacks.getValue().length === 0 && this.$dirty.getValue() === false)) {
      this.$init = true;

      this.$dirty.next(true);
    }

    return this.$feedbacks;
  }

  addFeedback(feedback: any): Observable<HttpResponseModel> {
    return this.$api.put('/feedback', {
      feedback,
    }).pipe(
      map((result: HttpResponseModel): HttpResponseModel => {
        this.$dirty.next(true);
        return result;
      }));
  }

  updateFeedback(id: number, feedback: any): Observable<HttpResponseModel> {
    return this.$api.patch(`/feedback/${id}`, {
      feedback,
    }).pipe(
      map((result: HttpResponseModel): HttpResponseModel => {
        this.$dirty.next(true);
        return result;
      }));
  }

  deleteFeedback(id: number): Observable<HttpResponseModel> {
    return this.$api.delete(`/feedback/${id}`).pipe(
      map((result: HttpResponseModel): HttpResponseModel => {
        this.$dirty.next(true);
        return result;
      }));
  }
}
