import { AfterContentInit, Component, OnInit } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { FeedbackModel } from '@models/feedback.model';
import { EnumerationModel } from '@models/enumeration.model';
import { BreakPointsService } from '@services/breakpoints.service';
import { EnumerationService } from '@services/enumeration.service';
import { EnumerationPlayComponent } from '@admin/feedbacks/enumeration-play/enumeration-play.component';
import { MatBottomSheet } from '@angular/material/bottom-sheet';
import { FeedbackService } from '@services/feedback.service';
import { FeedbackPlayComponent } from '@admin/feedbacks/feedback-play/feedback-play.component';

@Component({
  selector: 'app-feedbacks',
  templateUrl: './feedbacks.component.html',
  styleUrls: ['./feedbacks.component.css']
})
export class FeedbacksComponent implements OnInit, AfterContentInit {
  // Feedbacks table
  ftdc: string[] = ['id', 'question', 'enumerations', 'action'];
  ftds: MatTableDataSource<FeedbackModel>
    =
    new MatTableDataSource<FeedbackModel>([]);


  // Enumerations table
  etdc: string[] = ['id', 'label', 'action'];
  etds: MatTableDataSource<EnumerationModel>
    =
    new MatTableDataSource<EnumerationModel>([]);

  constructor(
    private $feedback: FeedbackService,
    private $enumeration: EnumerationService,
    private $bottomSheet: MatBottomSheet,
    public breakpoints: BreakPointsService,
  ) {
  }

  ngOnInit(): void {
  }

  ngAfterContentInit(): void {
    this.getFeedbacks();
    this.getEnumerations();
  }

  getFeedbacks(): void {
    this.$feedback.getFeedbacks().subscribe(
      (feedbacks: FeedbackModel[]) => {
        this.ftds.data = feedbacks;
      },
    );
  }

  getEnumerations(): void {
    this.$enumeration.getEnumerations().subscribe(
      (enumerations: EnumerationModel[]) => {
        this.etds.data = enumerations;
      },
    );
  }

  feedbackPlay(feedback: FeedbackModel | null): void {
    this.$bottomSheet.open(FeedbackPlayComponent, {
      data: {
        feedback,
      }
    });
  }

  enumerationPlay(enumeration: EnumerationModel | null): void {
    this.$bottomSheet.open(EnumerationPlayComponent, {
      data: {
        enumeration,
      }
    });
  }
}
